import { motion, AnimatePresence} from "framer-motion";
import Quiosco from "./quiosco.png";
import Deuda from "./pedir-prestado.png";
import ContenedorBotones from "../componentes-quiz/componente-botones";
import "../../App.css";

const EjerciciosAritmetica = ({ seccion, setDeshabilitadoPrincipal }) => {
  const TablaGolosinas = () => {
    const tablaStyle = {
      width: "100%",
      margin: "0 auto",
      borderCollapse: "collapse",
      boxShadow: "0 0 10px rgba(0,0,0,0.1)",
      background: "#fff",
      borderRadius: "10px",
      overflow: "hidden",
      transform: "translateY(-90px)",
    };

    const thStyle = {
      padding: "15px",
      backgroundColor: "#ffad60",
      color: "white",
      fontSize: "1.1em",
      textAlign: "center",
    };

    const tdStyle = {
      padding: "15px",
      textAlign: "center",
      color: "black",
    };

    const precioStyle = {
      ...tdStyle,
      fontWeight: "bold",
      color: "#2e7d32",
    };

    const filaPar = { backgroundColor: "#fff3e0" };

    const golosinas = [
      {
        producto: "Chocolatina",
        descripcion: "Chocolate con leche relleno de man",
        precio: "$1.00",
      },
      {
        producto: "Gomitas",
        descripcion: "Bolsa surtida de sabores frutales",
        precio: "$0.75",
      },
      {
        producto: "Caramelos",
        descripcion: "Caramelos duros de menta y fresa",
        precio: "$0.50",
      },
      {
        producto: "Paleta",
        descripcion: "Paleta de caramelo con centro de chicle",
        precio: "$0.60",
      },
      {
        producto: "Chicles",
        descripcion: "Paquete de 5 unidades sabor menta",
        precio: "$0.40",
      },
    ];

    return (
      <div className="contenedor-tabla" style={{ transform: "scale(0.7)" }}>
        <table style={tablaStyle}>
          <thead>
            <tr>
              <th style={thStyle}>Producto</th>
              <th style={thStyle}>Descripcin</th>
              <th style={thStyle}>Precio</th>
            </tr>
          </thead>
          <tbody>
            {golosinas.map((item, index) => (
              <tr key={index} style={index % 2 === 0 ? filaPar : {}}>
                <td style={tdStyle}>{item.producto}</td>
                <td style={tdStyle}>{item.descripcion}</td>
                <td style={precioStyle}>{item.precio}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  };

  const contenido = {
    0: (
      <div className="secciones">
        <p>
          Si pudieras percatarte en Innova School, hay estudiantes que
          observaron la necesidad de vender golosinas, snaks, o hasta incluso
          postres riquisimos debido a la limitada lista en los quioscos de la
          institucin y a los antojos de sus compaeros.
        </p>
        <img src={Quiosco} alt="Quiosco"/>
      </div>
    ),
    1: (
      <div className="secciones">
        <p>
          Sin embargo, como a veces la gente no lleva dinero, suelen fiar al
          vendedor para saciar su antojo o hambre. El vendedor para asegurarse
          que le paguen el fiado, empieza a cobrar intereses.
        </p>
        <img src={Deuda} alt="Deuda" />
      </div>
    ),
    2: (
      <div className="secciones">
        <p>
          Es por ello que se aplica el <strong>inters simple</strong>, una
          forma para calcular el monto a pagar considerando el dinero prestado y
          el inters acumulado.
        </p>
      </div>
    ),
    3: (
      <div className="secciones">
        <p>
          Antes de visualizar un caso, debemos de conocer algunos conceptos y
          trminos que nos ayudara a comprender mejor el tema.
        </p>
        <div className="seccion-ejercicios-formulas">
          <p>
            Capital: <b>C</b>
          </p>
          <p>
            Tiempo: <b>t</b>
          </p>
          <p>
            Tasa de inters: <b>r%</b>
          </p>
          <p>
            Monto: <b>M</b>
          </p>
        </div>
      </div>
    ),
    4: (
      <div className="secciones">
        <p>
          Imagina que tienes tu negocio de golosinas en el colegio y este es tu
          inventario.
        </p>
        <TablaGolosinas />
      </div>
    ),
    5: (
      <div className="secciones">
        <p>
          Un compaero tuyo te pide una gran cantidad de golosinas para un
          compartir en el aula
        </p>
        <ul style={{ textAlign: "left" }}>
          <li>15 paquetes de Cholocatina (15$)</li>
          <li>40 paquetes de gomitas (30$)</li>
          <li>50 caramelos (25$)</li>
        </ul>
      </div>
    ),
    6: (
      <div className="secciones">
        <p>Cunto seria el monto total que el compaero te debe?</p>
        <ul style={{ textAlign: "left", scale: "0.8" }}>
          <li>15 paquetes de Cholocatina (15$)</li>
          <li>40 paquetes de gomitas (30$)</li>
          <li>50 caramelos (25$)</li>
        </ul>
        <ContenedorBotones
          opciones={["90$", "75$", "70$", "60$"]}
          correcta={2}
          setDeshabilitadoPrincipal={setDeshabilitadoPrincipal}
        />
      </div>
    ),
    7: (
      <div className="secciones">
        <p>
          Al no tener dinero a la mano, dice que te pagar otro da. Es aqui
          donde aplicamos las reglas del inters simple
        </p>
        <h3>Paso 1</h3>
        <p>
          El monto total obtenido en el anterior ejercicio se vuelve el{" "}
          <b>Capital</b>
        </p>
        <p>
          La <b>tasa de inters</b> es una fraccin del capital que queremos
          recibir en un periodo de tiempo escrito en porcentaje, en nuestro
          caso, podemos tomar el 10% del Capital (7$) como valor para la tasa de
          inters
        </p>
      </div>
    ),
    8: (
      <div className="secciones">
        <h3>Paso 2</h3>
        <p>
          Luego de reconocer la tasa de inters, debemos de identificar el{" "}
          <b>tiempo</b> en el que aumentaremos el monto debido y que este
          coincida con el periodo de la <b>tasa de inters</b>
        </p>
        <div className="seccion-ejercicios-formulas">
          <p>
            A nuestra <b>tasa de inters</b> en periodos semanales, debemos de
            considerar el valor del <b>tiempo</b> en semanas, podemos tomar "3
            semanas" como un posible valor del <b>tiempo</b>
          </p>
        </div>
      </div>
    ),
    9: (
      <div className="secciones">
        <h3>Paso 3</h3>
        <p>
          Finalmente hallamos el inters con el cual cobraremos usando la
          siguiente frmula:
        </p>
        <div className="seccion-ejercicios-formulas" style={{ scale: "0.8" }}>
          <p>Inters = C x r% x t</p>
        </div>
        <p>
          Con ella, hallemos el <b>inters</b> si nuestro Capital es 70$,
          nuestra tasa de inters es 10% semanal y el tiempo es 3 semanas
        </p>
        <ContenedorBotones
          opciones={["21$", "7$", "30$", "70$"]}
          correcta={0}
          setDeshabilitadoPrincipal={setDeshabilitadoPrincipal}
        />
      </div>
    ),
   10: (
      <div className="secciones">
        <p>
          Excelente, el inters hallado es la cantidad que cobraremos a nuestro
          amigo ms el monto que le dimos, osea el capital. Para hallar el monto
          que debe pagar nuestro amigo, debemos de sumar el inters con el
          capital
        </p>
        <div className="seccion-ejercicios-formulas">
          <p>Monto = C + I</p>
        </div>
        <p>
          Hallemos el monto si nuestro Capital es 70$ y el inters del anterior
          ejercicio es 21$
        </p>
        <ContenedorBotones
          opciones={["81$", "91$", "76$", "70$"]}
          correcta={1}
          setDeshabilitadoPrincipal={setDeshabilitadoPrincipal}
        />
      </div>
    ),
    11: (
      <div className="secciones">
        <p>
          Bien, ahora ya tenemos idea de como aplicar el Inters Simple. A
          continuacin, resolveremos algunos casos en los que apliquemos ello
        </p>
      </div>
    ),
    12: (
      <div className="secciones">
        <h2>Caso 1</h2>
        <p>
          Cal ser el inters simple si nuestro capital es de 2000$, nuestra
          tasa de inters es del 8% anual durante 3 aos?
        </p>
        <ContenedorBotones
          opciones={["100$", "160$", "320$", "480$"]}
          correcta={3}
          setDeshabilitadoPrincipal={setDeshabilitadoPrincipal}
        />
      </div>
    ),
    13: (
      <div className="secciones">
        <h2>Dato:</h2>
        <p>
          En algunos problemas, el periodo en el que se aplica la tasa de
          inters (<b>r%</b>) puede ser diferente al periodo de nuestra variable
          tiempo (<b>t</b>). Por ejemplo:
        </p>
        <div className="seccion-ejercicios-formulas">
          <span style={{ display: "block" }}>
            Si "r%" es igual a 5% <b>semestral</b> Y "t" es de 4 <b>aos</b>,
            debemos de generar equivalencias:
          </span>
          <span>5% semestral es equivalente a 10% anual</span>
        </div>
      </div>
    ),
    14: (
      <div className="secciones">
        <h2>Caso 2</h2>
        <p>
          Si Mariano ha prestado 12 000$ a un amigo con una tasa de inters al
          10% bimestral para 8 meses Cul es el{" "}
          <b style={{ textDecoration: "underline" }}>monto</b> que pagar luego
          de este periodo
        </p>
        <ContenedorBotones
          opciones={["16 800$", "4800$", "1200$", "1080$"]}
          correcta={0}
          setDeshabilitadoPrincipal={setDeshabilitadoPrincipal}
        />
      </div>
    ),
    15: (
      <div className="secciones">
        <h2>Dato:</h2>
        <p>
          Algunos problemas pueden ser muy complejos en su comprensin, como el
          siguiente:
        </p>
        <p>
          Durante cuanto <b>tiempo</b> (1) Juan debe depositar en un banco un{" "}
          <b>capital</b> de $25 000 (2) al 5% (3) <br />
          para que se convierta es $30 000 (4)?
        </p>
        <ul style={{ textAlign: "left" }}>
          <li>1: tiempo (t)= x</li>
          <li>2: capital (C)= $25 000</li>
          <li>3: tasa de inters (r%)= 5% anual</li>
          <li>4: monto (M)= $30 000</li>
        </ul>
      </div>
    ),
     16: (
      <div className="secciones">
        <h2>Dato</h2>
        <p>
          Primeros hallemos el <b>inters</b> en base al monto
        </p>
        <p>Monto = Inters + Capital</p>
        <ContenedorBotones
          opciones={["3000$", "4000$", "500$", "5000$"]}
          correcta={3}
          setDeshabilitadoPrincipal={setDeshabilitadoPrincipal}
        />
      </div>
    ),
    17: (
      <div className="secciones">
        <h2>Dato</h2>
        <p>
          Si el inters es 5000$, consideremos entonces la frmula del inters
          para hallar el tiempo
        </p>
        <div className="seccion-ejercicios-formulas">
          <p>I = C x r% x t</p>
        </div>
        <ContenedorBotones
          opciones={["4 aos", "6 aos", "3 aos", "5 aos"]}
          correcta={0}
          setDeshabilitadoPrincipal={setDeshabilitadoPrincipal}
        />
      </div>
    ),
    18: (
      <div className="secciones">
        <h2>Caso 3</h2>
        <p>
          Calculemos el inters producido por $3000 impuestos <u>al 15%</u>{" "}
          durante tres aos
        </p>
        <p>
          Dato: Si no se menciona el periodo de "r%" se sobreentiende que el
          periodo es anual
        </p>
        <ContenedorBotones
          opciones={["$1305", "$1035", "$1350", "$1530"]}
          correcta={2}
          setDeshabilitadoPrincipal={setDeshabilitadoPrincipal}
        />
      </div>
    ),
    19: (
      <div className="secciones">
        <h2>Caso 4</h2>
        <p>
          Calulemos el inters que produce un capital de $1200 colocados a una
          tasa del 4% mensual durante cinco bimestres
        </p>
        <ContenedorBotones
          opciones={["$840", "$804", "$408", "$480"]}
          correcta={3}
          setDeshabilitadoPrincipal={setDeshabilitadoPrincipal}
        />
      </div>
    ),
     20: (
      <div className="secciones">
        <h2>Caso 5</h2>
        <p>Jniffer deposita $20 000 en una entidad financiera durante 3 aos a una tasa del 12,5% semestral. Calcula el tiempo</p>
        <ContenedorBotones
          opciones={["$15 000", "$30 000", "$7500", "$44 000"]}
          correcta={0}
          setDeshabilitadoPrincipal={setDeshabilitadoPrincipal}
        />
      </div>
    ),
  };

  return (
    <AnimatePresence mode="wait">
      <motion.div
        key={seccion}
        initial={{ opacity: 0, y: 50 }}
        animate={{ opacity: 1, y: 0 }}
        exit={{ opacity: 0, y: -50 }}
        transition={{ duration: 1 }}
        className="seccion-ejercicios-aritmetica"
      >
        {contenido[seccion] || <p>Contenido no disponible.</p>}
      </motion.div>
    </AnimatePresence>
  );
};

export default EjerciciosAritmetica;
