import { motion, AnimatePresence } from "framer-motion";
import ContenedorBotones from "../componentes-quiz/componente-botones";
import "../../App.css";

const EjerciciosTrigonometria = ({ seccion, setDeshabilitadoPrincipal }) => {
  const contenido = {
    0: (
      <div className="secciones">
        <h2>Razones trigonomtricas de un ngulo agudo</h2>
        <p>
          Las razones trigonomtricas relacionan los lados de un tringulo rectngulo con sus ngulos. Son fundamentales para resolver problemas de tringulos y muchas aplicaciones en ciencias e ingeniera.
        </p>
      </div>
    ),
    1: (
      <div className="secciones">
        <h3>Qu es un ngulo agudo?</h3>
        <p>
          Un <b>ngulo agudo</b> es aquel que mide ms de 0 y menos de 90.
        </p>
      </div>
    ),
    2: (
      <div className="secciones">
        <h3>Tringulo rectngulo y sus lados</h3>
        <ul>
          <li><b>Hipotenusa:</b> Lado opuesto al ngulo recto (el ms largo).</li>
          <li><b>Cateto opuesto:</b> Lado opuesto al ngulo agudo considerado.</li>
          <li><b>Cateto adyacente:</b> Lado que forma el ngulo agudo junto con la hipotenusa.</li>
        </ul>
      </div>
    ),
    3: (
      <div className="secciones">
        <h3>Razones trigonomtricas principales</h3>
        <ul>
          <li><b>Seno (sen):</b> cateto opuesto / hipotenusa</li>
          <li><b>Coseno (cos):</b> cateto adyacente / hipotenusa</li>
          <li><b>Tangente (tan):</b> cateto opuesto / cateto adyacente</li>
        </ul>
      </div>
    ),
    4: (
      <div className="secciones">
        <h3>Ejercicio: Identifica la razn trigonomtrica</h3>
        <p>Cul es la razn trigonomtrica que relaciona el cateto opuesto y la hipotenusa?</p>
        <ContenedorBotones
          opciones={["Seno", "Coseno", "Tangente", "Secante"]}
          correcta={0}
          setDeshabilitadoPrincipal={setDeshabilitadoPrincipal}
        />
      </div>
    ),
    5: (
      <div className="secciones">
        <h3>Ejercicio: Calcula una razn trigonomtrica</h3>
        <p>
          En un tringulo rectngulo, el cateto opuesto mide 3 y la hipotenusa 5. Cul es el seno del ngulo agudo?
        </p>
        <ContenedorBotones
          opciones={["3/5", "4/5", "3/4", "5/3"]}
          correcta={0}
          setDeshabilitadoPrincipal={setDeshabilitadoPrincipal}
        />
      </div>
    ),
    // Puedes agregar ms secciones y ejercicios segn lo necesites
  };

  return (
    <AnimatePresence mode="wait">
      <motion.div
        key={seccion}
        initial={{ opacity: 0, y: 50 }}
        animate={{ opacity: 1, y: 0 }}
        exit={{ opacity: 0, y: -50 }}
        transition={{ duration: 1 }}
        className="seccion-ejercicios-trigonometria"
      >
        {contenido[seccion] || <p>Contenido no disponible.</p>}
      </motion.div>
    </AnimatePresence>
  );
};

export default EjerciciosTrigonometria;